import { Input, Component, OnInit } from '@angular/core';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';
import { Subject } from 'rxjs/Subject';
import { IAlert } from '../interfaces/i-alert';
import { IHero } from '../interfaces/i-hero';

@Component({
  selector: 'app-root',
  templateUrl: './heroes.component.html',
  styleUrls: ['./heroes.component.css']
})
export class HeroesComponent implements OnInit {

  @Input()
  public alerts: Array<IAlert> = [];

  items: FirebaseListObservable<any[]>;
  hpSubject: Subject<any>;
  closeResult: string;
  newHero: IHero;

  constructor(private db: AngularFireDatabase, private modalService: NgbModal) {
    this.hpSubject = new Subject();
    this.hpSubject.next(100);
    this.items = db.list('/heroes', {
      query: {
        orderByChild: 'hp'
      }
    });
    this.resetNewHero();

    this.alerts.push({
      id: 1,
      type: 'info',
      message: '歡迎使用英雄資料編輯功能。',
    });
  }

  getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  resetNewHero(): void {
    this.newHero = {
      name: "",
      hp: 0,
      mp: 0,
      str: 0,
      dex: 0,
      int: 0
    };
  }

  onInsert(): void {

    if (this.newHero.name.trim().length === 0) {
      while (this.alerts.length > 0) {
        this.alerts.pop();
      }

      this.alerts.push({
        id: 1,
        type: 'danger',
        message: '新增時必須輸入姓名。',
      });

    } else {
      this.items.push(this.newHero);
      this.resetNewHero();

      while (this.alerts.length > 0) {
        this.alerts.pop();
      }

      this.alerts.push({
        id: 1,
        type: 'success',
        message: '新增完成。',
      });
    }


    // this.modalService.open("test").result.then((result) => {
    //   this.closeResult = `Closed with: ${result}`;
    // }, (reason) => {
    //   this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    // });

  }

  onUpdate(item): void {
    var itemObservable = this.db.object('/heroes/' + item.$key);
    itemObservable.update(item);
  }


  onDelete(item): void {
    this.items.remove(item);
  }

  ngOnInit() {
  }



}



